<template>
    <div class="g-in">
        <div>
            <ul class="menu-wrap">
                <li v-for="(i, index) in comboxes" :key="index" @click="valueName = index;" :class="valueName===index?'menu-active':''"> {{i.name}}
                </li>
            </ul>
            <section class="news-section" style="position:relative;">
                <div class="map-wrap">
                    <echarts-map :color="'#000'" :height="'800px'" :width="'1000px'"></echarts-map>
                </div>
            </section>
        </div>
    </div>
</template>
<script>
    import echartsMap from '../home/components/echarts-map.vue'
    export default {
        components: {
            echartsMap
        },
        data() {
            return {
                valueName: 0,
                comboxes: []
            }
        },
        created() {
            this.$ajaxPost(this.projectName + '/index/finds.do').then(res => {
                // console.log(res)
                this.comboxes = res.allList.filter(item => item.parentId === 0);
                this.comboxes.reverse()
            })
        },
        methods: {
            initMap() {}
        }
    }
</script>
<style lang="scss" scoped>
    .map-wrap {
        width: 1000px;
        height: 800px;
        transform: translateX(-50%);
        left: 50%;
        position: relative;
    }
    .news-section {
        padding-bottom: 30px;
        min-height: 457px;
        background-color: #fff;
        margin-bottom: 32px;
    }
    .menu-wrap {
        height: 60px;
        line-height: 60px;
        box-shadow: 0 1px 6px rgba(0, 0, 0, .2);
        padding: 0 30px;
        margin: 32px 0;
        background-color: #fff;
        li {
            cursor: pointer;
            width: 80px;
            height: 32px;
            text-align: center;
            line-height: 32px;
            display: inline-block;
            &+li {
                margin-left: 20px;
            }
        }
    }
    .menu-active {
        color: #f65c5c;
        border: 1px solid #f65c5c;
        border-radius: 6px;
        box-sizing: border-box;
    }
</style>





